import React, {Component, PropTypes} from 'react';

class Shell extends Component {

    // constructor() {
    //
    //     super();
    //     this.generateAddButton = this.generateAddButton.bind(this);
    //
    // }

    generateAddButton() {

        const {addCallback} = this.props.shell;

        switch (true) {

            case typeof addCallback === 'function':
                return (
                    <div className="Shell-button">
                        <button>New</button>
                    </div>
                );

            default:
                return '';

        }

    }

    render() {

        const {children, shell: {heading}} = this.props;

        return (
            <div className="Shell">

                <header className="Shell-header">
                    <h2 className="Shell-heading">{heading}</h2>
                    {this.generateAddButton()}
                </header>

                <div className="Shell-content">
                    {children}
                </div>

            </div>
        );

    }

}

export default Shell;



