import React, {Component, PropTypes} from 'react';
import Shell from '../shell/view';

class Topics extends Component {

    generateTopic({heading, feedback, addCallback}, key) {

        return (
            <article className="Topics-topic">
                <Shell shell={{heading, addCallback}} key={key}>
                    {feedback}
                </Shell>
            </article>
        );

    }

    render() {

        const positive = {
            heading: 'Positive',
            feedback: 'positive',
            addCallback: () => console.log('Add button (positive)')
        };

        const negative = {
            heading: 'Negative',
            feedback: 'negative',
            addCallback: () => console.log('Add button (negative)')
        };

        return (
            <section className="Topics">
                {[positive, negative].map(this.generateTopic)}
            </section>
        );

    }

}

export default Topics;



